import { Router } from "@angular/router";
import { AuthService } from "./../../services/auth.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import { IonSlides, LoadingController, NavController } from "@ionic/angular";
import * as moment from "moment";
import { UserService } from "src/app/services/user.service";

@Component({
  selector: "app-sleep-schedule",
  templateUrl: "./sleep-schedule.page.html",
  styleUrls: ["./sleep-schedule.page.scss"],
})
export class SleepSchedulePage implements OnInit {
  @ViewChild("slideDate") dateslide: IonSlides;
  currentDate: any;
  currentWeek = 0;
  days = [];
  nome;
  initDate: any;
  selectedDate;
  sleepUser;
  horaIni;
  difHora;
  difAcordar;
  horaAcordar;
  arrHorasSono;
  horaSono;
  constructor(
    private navctr: NavController,
    private authSrv: AuthService,
    private router: Router,
    private userSrv: UserService,
    private loadCtrl: LoadingController
  ) {
    setTimeout(() => {
      this.dateConfi();
    }, 1000);
  }

  async ngOnInit() {
    await this.loadProfile();
    await this.loadSleepUser();
  }

  loadProfile() {
    this.authSrv.profile().subscribe((data) => {
      this.nome = data.nome;
      console.log(this.nome);
    });
  }

  dateConfi() {
    setTimeout(() => {
      this.dateslide.lockSwipes(true);
    }, 150);
    this.selectedDate = moment().format().split("T")[0];
    this.currentDate = moment().format().split("T")[0];
    this.days = [];
    this.initDate = moment().format();
    const startOfWeek = moment().locale("pt-br").startOf("week");
    const endOfWeek = moment().endOf("week");
    let day = startOfWeek;
    while (day <= endOfWeek) {
      const data = {
        date: day.toDate().getDate(),
        day: day.format("ddd  "),
        val: day.format(),
        selectDay: day.format("dddd DD MMMM"),
      };
      this.days.push(data);
      day = day.clone().add(1, "d");
    }
  }
  previousWeek() {
    if (this.currentWeek !== 0) {
      this.dateslide.update();
      this.days = [];
      this.currentWeek = this.currentWeek - 1;
      const startOfWeek = moment()
        .add(this.currentWeek, "weeks")
        .startOf("week");
      const endOfWeek = moment().add(this.currentWeek, "weeks").endOf("week");
      let day = startOfWeek;
      while (day <= endOfWeek) {
        const data = {
          date: day.toDate().getDate(),
          day: day.format("ddd"),
          val: day.format(),
          selectDay: day.format("dddd DD MMMM"),
        };
        this.days.push(data);
        day = day.clone().add(1, "d");
      }
      console.log(this.days);
    }
  }
  selectDate(date) {
    console.log(date);
    if (this.currentDate <= date.val.split("T")[0]) {
      this.selectedDate = date.val.split("T")[0];
      console.log(this.selectedDate);
    }
  }
  nextWeek() {
    this.dateslide.update();
    this.days = [];
    this.currentWeek++;
    console.log(this.currentWeek);
    const startOfWeek = moment().add(this.currentWeek, "weeks").startOf("week");
    const endOfWeek = moment().add(this.currentWeek, "weeks").endOf("week");
    let day = startOfWeek;
    while (day <= endOfWeek) {
      const data = {
        date: day.toDate().getDate(),
        day: day.format("ddd"),
        val: day.format(),
        selectDay: day.format("dddd DD MMMM"),
      };
      this.days.push(data);
      day = day.clone().add(1, "d");
    }
    console.log(this.days);
  }

  onClick() {
    this.router.navigate(["add-alarm"]);
  }

  async loadSleepUser() {
    try {
      const data = await this.userSrv.getSleepUser().toPromise();
      this.sleepUser = data;
      this.horaIni = data.horario_dormir;
      this.difHora = this.calcularDiferencaDeTempo(this.horaIni);
      this.horaAcordar = this.somarHoras(this.horaIni, data.horas_sono);
      this.arrHorasSono = data.horas_sono.split(":");
      if (this.arrHorasSono[1] !== '00') {
        this.horaSono = this.arrHorasSono[0] + ' horas e ' + this.arrHorasSono[1] + ' minutos';
      } else {
        this.horaSono = this.arrHorasSono[0] + ' horas ';
        if (this.horaSono[0] === '0') {
          this.horaSono = this.horaSono[1] + ' horas';
        }
      }
    } catch (error) {
      console.error("Erro ao carregar informações de sono:", error);
    }

    
  }
  

  calcularDiferencaDeTempo(horaFornecida: string): string {
    const horaAtual = new Date();
    const [hora, minuto, segundo] = horaFornecida.split(":").map(Number);

    if (isNaN(hora) || isNaN(minuto) || isNaN(segundo)) {
      return "Formato de hora fornecida inválido. Use o formato HH:MM:SS.";
    }

    const horaFornecidaData = new Date(horaAtual);
    horaFornecidaData.setHours(hora, minuto, segundo);

    if (horaFornecidaData < horaAtual) {
      return "A hora fornecida deve ser no futuro.";
    }

    const diferencaMilissegundos =
      horaFornecidaData.getTime() - horaAtual.getTime();
    const diferencaSegundos = Math.floor(diferencaMilissegundos / 1000);
    const diferencaHoras = Math.floor(diferencaSegundos / 3600);
    const diferencaMinutos = Math.floor((diferencaSegundos % 3600) / 60);

    if (diferencaHoras === 0 && diferencaMinutos === 0) {
      return "A hora fornecida é agora!";
    }

    let resultado = "Em ";
    if (diferencaHoras > 0) {
      resultado += `${diferencaHoras} hora${diferencaHoras > 1 ? "s" : ""}`;
      if (diferencaMinutos > 0) {
        resultado += ` e ${diferencaMinutos} minuto${
          diferencaMinutos > 1 ? "s" : ""
        }`;
      }
    } else {
      resultado += `${diferencaMinutos} minuto${
        diferencaMinutos > 1 ? "s" : ""
      }`;
    }

    return resultado;
  }

  somarHoras(hora1: string, hora2: string): string {
    const [h1, m1, s1] = hora1.split(":").map(Number);
    const [h2, m2, s2] = hora2.split(":").map(Number);

    if (
      isNaN(h1) ||
      isNaN(m1) ||
      isNaN(s1) ||
      isNaN(h2) ||
      isNaN(m2) ||
      isNaN(s2)
    ) {
      return "Formato de hora inválido. Use o formato HH:MM:SS.";
    }

    const totalSegundos = h1 * 3600 + m1 * 60 + s1 + (h2 * 3600 + m2 * 60 + s2);
    let horas = Math.floor(totalSegundos / 3600);
    const minutos = Math.floor((totalSegundos % 3600) / 60);

    if (horas === 0 && minutos === 0) {
      return "Não houve alteração no horário.";
    }

    horas = horas % 24;
    let resultado = "Você acordará às ";
    if (horas >= 12) {
      if (horas > 12) {
        horas -= 12;
      }
      resultado += `${horas} hora${horas > 1 ? "s" : ""}`;
      if (minutos > 0) {
        resultado += ` e ${minutos} minuto${minutos > 1 ? "s" : ""}`;
      }
      resultado += " da tarde";
    } else {
      if (horas === 0) {
        horas = 12; // Meia-noite
      }
      resultado += `${horas} hora${horas > 1 ? "s" : ""}`;
      if (minutos > 0) {
        resultado += ` e ${minutos} minuto${minutos > 1 ? "s" : ""}`;
      }
      resultado += " da manhã";
    }

    return resultado;
  }
  
  onBack() {
    this.navctr.back();  
  }
}
